import React from 'react'
import { render } from 'react-dom'
import App from 'App'

const root = document.getElementById('root')

render(<App />, root)

if (module.hot) {
  module.hot.accept('./App', () => {
    const NextApp = require('./App').default // eslint-disable-line no-console
    render(<NextApp />, root)
  })
}
